import React, { useState } from "react";
import { Card, Typography, Modal, Table, Spin } from "antd";
import { useGetBookingsByCompanyQuery } from "../../../../Redux/Company/companyApiSlice";

const { Title } = Typography;

const TotalBookingsCard = () => {
  const [isModalVisible, setIsModalVisible] = useState(false);

  // API call to get bookings
  const { data: bookingData, isLoading, isError } = useGetBookingsByCompanyQuery();

  // Calculate total bookings
  const totalBookings = bookingData?.length || 0;

  // Columns for booking list table
  const columns = [
    {
      title: "STT",
      dataIndex: "index",
      key: "index",
      render: (_, __, index) => index + 1,
    },
    {
      title: "Tên Người Dùng",
      dataIndex: "userName",
      key: "userName",
    },
    {
      title: "Ngày Đặt",
      dataIndex: "bookingDate",
      key: "bookingDate",
    },
    {
      title: "Tổng Giá",
      dataIndex: "totalPrice",
      key: "totalPrice",
      render: (value) =>
        new Intl.NumberFormat("vi-VN", { style: "currency", currency: "VND" }).format(value),
    },
  ];

  // Prepare data for table
  const tableData =
    bookingData?.map((booking, index) => ({
      key: index,
      userName: booking.user.fullName,
      bookingDate: new Date(booking.bookingDate).toLocaleDateString(),
      totalPrice: booking.totalPrice,
    })) || [];

  return (
    <>
      {/* Booking Summary Card */}
      <Card
        style={{
          backgroundColor: "#fff",
          borderRadius: "8px",
          boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
          cursor: "pointer",
        }}
        onClick={() => setIsModalVisible(true)} // Open modal on click
      >
        <Title level={5}>Tổng Số Vé Đặt</Title>
        <p style={{ fontSize: "18px", fontWeight: "bold" }}>
          {isLoading ? (
            <Spin size="small" />
          ) : isError ? (
            "Lỗi tải dữ liệu"
          ) : (
            totalBookings
          )}
        </p>
        <p style={{ color: "#52c41a", fontWeight: "bold" }}>+3.48% So với tháng trước</p>
      </Card>

      {/* Modal for Booking List */}
      <Modal
        title="Danh Sách Vé Đặt"
        visible={isModalVisible}
        onCancel={() => setIsModalVisible(false)}
        footer={null}
        width={800}
      >
        {isLoading ? (
          <Spin tip="Đang tải dữ liệu..." />
        ) : isError ? (
          <p style={{ color: "red" }}>Lỗi khi tải dữ liệu!</p>
        ) : (
          <Table
            columns={columns}
            dataSource={tableData}
            pagination={{ pageSize: 10 }}
            bordered
            size="small"
          />
        )}
      </Modal>
    </>
  );
};

export default TotalBookingsCard;
