import React from "react";
import { Layout, Row, Col, Card, Typography } from "antd";
import Sidebar from "../components/Sidebar";
import CompletedTripsChart from "../components/Revenue/CompletedTripsChart";
import PaymentMethodChart from "../components/Revenue/RevenueChart";
import Top10UsersTable from "../components/Revenue/UserGrowthChart";
import TotalBookingsCard from "../components/Revenue/TotalBookingsCard"; // Import TotalBookingsCard

const { Header, Content } = Layout;
const { Title } = Typography;

const Dashboard = () => {
  const summaryCards = [
    {
      title: "Doanh Thu",
      value: "2,356,000 ₫",
      growth: "+12.18%",
    },
    {
      title: "Số Lượt Bán",
      value: 924,
      growth: "-5.72%",
    },
    {
      title: "Tỷ Lệ Hoàn Thành",
      value: "49.65%",
      growth: "+54.8%",
    },
  ];

  return (
    <Layout style={{ minHeight: "100vh" }}>
      {/* Sidebar */}
      <Sidebar />

      <Layout>
        <Header
          style={{
            backgroundColor: "#fff",
            padding: "10px 20px",
            display: "flex",
            justifyContent: "space-between",
          }}
        >
          <Title level={4} style={{ margin: 0 }}>
            Bảng Điều Khiển
          </Title>
          <div>Admin Công Ty Xe Toàn</div>
        </Header>
        <Content style={{ padding: "20px", backgroundColor: "#f0f2f5" }}>
          {/* Summary Cards */}
          <Row gutter={[16, 16]}>
            <Col xs={24} sm={12} md={6}>
              <TotalBookingsCard /> {/* Sử dụng Component riêng cho Tổng Vé Đặt */}
            </Col>
            {summaryCards.map((card, index) => (
              <Col key={index} xs={24} sm={12} md={6}>
                <Card
                  style={{
                    backgroundColor: "#fff",
                    borderRadius: "8px",
                    boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
                  }}
                >
                  <Title level={5}>{card.title}</Title>
                  <p style={{ fontSize: "18px", fontWeight: "bold" }}>{card.value}</p>
                  <p
                    style={{
                      color: card.growth.includes("+") ? "#52c41a" : "#ff4d4f",
                      fontWeight: "bold",
                    }}
                  >
                    {card.growth} So với tháng trước
                  </p>
                </Card>
              </Col>
            ))}
          </Row>

          {/* Top 10 Users Table */}
          <Row gutter={[16, 16]} style={{ marginTop: "20px" }}>
            <Col xs={24} md={12}>
              <Card title="Doanh Thu Theo Phương Thức Thanh Toán">
                <PaymentMethodChart />
              </Card>
            </Col>
            <Col xs={24} md={12}>
              <Top10UsersTable />
            </Col>
          </Row>

          <Row gutter={[16, 16]} style={{ marginTop: "20px" }}>
            <Col xs={24}>
              <Card title="Thống Kê Chuyến Đi">
                <CompletedTripsChart />
              </Card>
            </Col>
          </Row>
        </Content>
      </Layout>
    </Layout>
  );
};

export default Dashboard;
